import React from 'react'
import { Link, graphql } from "gatsby"

import Container from '../components/container'
import Head from '../components/head'
import Img from 'gatsby-image'
import blogStyles from './blog-post.module.css'

class BlogPostTemplate extends React.Component {
    render() {
      const post = this.props.data.markdownRemark
      const { previous, next } = this.props.pageContext
  
      return (
        <Container>
            <Head title={post.frontmatter.title}
                description={post.frontmatter.description || post.excerpt}/>

            <article>

                <div className={blogStyles.articleTD}>
                    <h2>
                        {post.frontmatter.title}
                    </h2>
                    <h4>{post.frontmatter.description}</h4>
                    <p> {post.frontmatter.duration} &nbsp; | &nbsp; {post.frontmatter.date} </p>
                    
                    <div className={blogStyles.imageCont}>
                        {!!post.frontmatter.featuredImage && !!post.frontmatter.featuredImage.childImageSharp
                            ? <Img fluid={post.frontmatter.featuredImage.childImageSharp.fluid}
                            alt={"Featured"} />
                            : <img src={post.frontmatter.featuredImage} alt={"Featured"} />
                        }
                        <p>{post.frontmatter.caption}</p>
                    </div>
                </div>

                <section className={blogStyles.sectionCont} dangerouslySetInnerHTML={{ __html: post.html }} />

            </article>


            <div className={blogStyles.nav}>
                <ul>

                    <li className={blogStyles.previousArticle}>
                        {previous && (
                            <div>
                                <p> ← </p>
                                <Link to={ previous.fields.slug} rel="prev">
                                    {previous.frontmatter.title}
                                </Link>
                            </div>
                        )}
                    </li>

                    <li className={blogStyles.nextArticle}>
                        {next && (
                            <div>
                                <p> → </p>
                                <Link to={ next.fields.slug} rel="next">
                                    {next.frontmatter.title}
                                </Link>
                            </div>
                        )}
                    </li>

                </ul>
            </div>

        </Container>
      )
    }
  }
  
  export default BlogPostTemplate
  
  export const pageQuery = graphql`
    query BlogPostBySlug($slug: String!) {
      markdownRemark(fields: { slug: { eq: $slug } }) {
        id
        excerpt(pruneLength: 160)
        html
        frontmatter {
          title
          date(formatString: "MMMM DD, YYYY")
          description
          duration
          caption
          featuredImage {
              childImageSharp {
                fluid(maxWidth: 1080) {
                    ...GatsbyImageSharpFluid
                  }
              }
          }
        }
      }
    }
  `